@import './variables.scss';
$primary-color: #FF6060;
$text-color: #000000;
$link-color: #000000;


@import './variables.scss';

.error404 {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    padding: 2rem;
    text-align: center;

    &__code {
        width: 59.7rem;
        height: 26.3rem;
        font-size: 18rem;
        font-weight: bold;
        color: $primary-color;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__message {
        margin-top: 13.9rem;
        font-size: 2.4rem;
        color: $text-color;
    }

    &__link {
        margin-top: 13rem;
        font-size: 1.6rem;
        text-decoration: underline;
        color: $link-color;
        transition: opacity 0.3s;

        &:hover {
            opacity: 0.7;
        }
    }
}

// Responsive mobile
@media (max-width: 768px) {
    .error404__code {
        width: auto;
        height: auto;
        font-size: 10rem;
    }

    .error404__message {
        margin-top: 6rem;
        font-size: 1.6rem;
    }

    .error404__link {
        margin-top: 6rem;
        font-size: 1.4rem;
    }
}

